import React from 'react';
import PropTypes from 'prop-types';
import { ResponsiveTreeMap } from '@nivo/treemap';
import chroma from 'chroma-js';
import Tooltip from './Tooltip';

const fromColor = '#7fe8c4';
const toColor = '#205e49';
const innerBorderColor = 'black';

const getColor = (d, scale) => {
  if (d.depth === 0) {
    return innerBorderColor;
  }

  if (d.depth === 1) {
    return innerBorderColor;
  }
  return scale(d.hours).hex();
};

const getTooltip = (obj) => {
  return (
    <Tooltip value={obj.data.hours} color={obj.color} name={obj.label} />
  );
};

const Treemap = ({ data }) => {
  const min = data.min_hours;
  const max = data.max_hours;
  const scale = chroma.scale([fromColor, toColor]).domain([min, max]);
  const getLabel = (d) => {
    return d.name;
  };

  return (
    <ResponsiveTreeMap
      tooltip={getTooltip}
      root={data}
      identity="id"
      value="hours"
      innerPadding={3}
      outerPadding={3}
      leavesOnly
      margin={{
        top: 10,
        right: 10,
        bottom: 10,
        left: 10,
      }}
      label={getLabel}
      labelSkipSize={15}
      labelTextColor="inherit:darker(3.5)"
      colorBy={d => getColor(d, scale, min, max)}
      borderColor="inherit:darker(0.3)"
      animate
      motionStiffness={90}
      motionDamping={11}
    />
  );
};

Treemap.propTypes = {
  data: PropTypes.object.isRequired,
};

export default Treemap;