import {
    FETCH_GET_MILESTONES,
    FETCH_MILESTONE_ASSIGN,
    FETCH_MILESTONE_UNASSIGN,
    FETCH_GET_MILESTONE,
    FETCH_CREATE_MILESTONE,
    FETCH_DELETE_MILESTONE,
    FETCH_UPDATE_MILESTONE,
    FETCH_GET_MILESTONE_TASKS,
    FETCH_GET_MILESTONES_HOURS_BOARD,
    FETCH_GET_MILESTONES_PERCENTAGE_HOURS,
} from '../../constants/api/milestones';

export const fetchGetMilestones = (id) => ({
    type: FETCH_GET_MILESTONES,
    fetch: true,
    url: `/milestones/get_milestone?project_id=${id}`,
    params: {
        method: 'GET',
    },
});

export const fetchMilestoneAssign = (body) => ({
    type: FETCH_MILESTONE_ASSIGN,
    fetch: true,
    url: `/milestones/add_task_to_milestone?task_id=${body.task_id}&milestone_id=${body.milestone_id}`,
    params: {
        method: 'POST',
        body,
    },
});

export const fetchMilestoneUnassign = (body) => ({
    type: FETCH_MILESTONE_UNASSIGN,
    fetch: true,
    url: `/milestones/remove_task_from_milestone?task_id=${body.task_id}&milestone_id=${body.milestone_id}`,
    params: {
        method: 'POST',
        body,
    },
});

