import {
  FETCH_ADD_HOURS,
  FETCH_GET_HOURS,
  FETCH_DELETE_HOUR,
} from '../../constants/api/hours';

export const fetchGetHours = id => ({
  type: FETCH_GET_HOURS,
  fetch: true,
  url: `/tasks/${id}/hours`,
  params: {
    method: 'GET',
  },
});

export const fetchAddHours = (id, body) => ({
  type: FETCH_ADD_HOURS,
  fetch: true,
  url: `/tasks/${id}/hours`,
  params: {
    method: 'POST',
    body,
  },
});

export const fetchDeleteHour = id => ({
  type: FETCH_DELETE_HOUR,
  fetch: true,
  url: `/hours/${id}`,
  params: {
    method: 'DELETE',
  },
});
