import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import CircularProgress from '../../../lib/CircularProgress';
import moment from 'moment';
import DayPickerInput from 'react-day-picker/DayPickerInput';

import { notifyError } from '../../../../actions/notification';
import { fetchGetProjectBoards } from '../../../../actions/api/boards';
import { fetchProjectMembers } from '../../../../actions/api/members';
import { fetchGetReportedTasksBoard } from '../../../../actions/api/boards';

import language from '../../../../constants/language';
import TaskProgress from './TaskProgress';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';


