import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import moment from 'moment';

import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';

import DayPickerInput from 'react-day-picker/DayPickerInput';
import { truncateString } from '../../../../utils';
import 'react-day-picker/lib/style.css';

import CommitViz from './Commits';
import CircularProgress from '../../../lib/CircularProgress';

import {
  fetchVizHoursPerTask,
  fetchVizGithub,
  fetchGetLinkedRepos,
} from '../../../../actions/api/viz';
import { changeTrazabilityHours } from '../../../../actions/project';
import { allMembers } from '../../../../selectors/members';
import { notifyError } from '../../../../actions/notification';
import language from '../../../../constants/language';

/* eslint-disable max-len */
