import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import CircularProgress from '../../../lib/CircularProgress';

import { notifyError } from '../../../../actions/notification';
import { fetchGetMilestones, fetchGetMilestonesPercentageHours } from '../../../../actions/api/milestones';

import language from '../../../../constants/language';
import PercentageRange from './PercentageRange';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';


const styles = {
  wrapper: {
    height: '400px',
    width: 'auto',
  },
  modalContent: {
    width: '600px',
    height: '400px',
  },
  modalContainer: {
    // width: '800px',
  },
  title: {
    fontSize: '28px',
    fontFamiliy:
      '-apple-system, ".SFNSText-Regular", "San Francisco", BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", "Lucida Grande", Arial, sans-serif',
    color: 'rgb(68, 68, 68)',
  },
  headerContainer: {
    margin: '30px',
    width: 'auto',
  },
  text: {
    color: '#2a3d41',
    fontSize: 16,
    fontWeight: 'bold',
    marginTop: '10px',
    marginBottom: '10px',
  },
  selectText: {
    color: '#2a3d41',
    fontSize: 14,
    fontWeight: 'normal',
    marginTop: '10px',
    marginBottom: '10px',
    marginLeft: '10px',
  },
  grid: {
    display: 'flex',
    width: '100%',
    margin: '10px',
  },
  line: {
    width: 'auto',
    height: '1px',
    backgroundColor: '#CCCCCC',
  },
  info: {
    color: '#2a3d41',
    fontSize: 16,
    fontWeight: 'bold',
    marginTop: '5px',
  },
  box: {
    display: 'flex',
    marginRight: '10px',
    padding: '5px',
  },
  customIcon: {
    height: '24px',
    width: '24px',
    display: 'block',
    'border-radius': '50%',
    marginRight: '5px',
  },
  shareText: {
    fontSize: 12,
    color: 'white',
    margin: '0px 0px',
  },
  shareButton: {
    margin: 5,
    padding: '0px 8px',
    borderColor: 'white',
    border: '1px solid #fff',
    backgroundColor: '#ea5941',
    '&:hover': {
      backgroundColor: '#ca3921',
    },
    color: '#fff',
    borderRadius: 0,
  },
};


class EstimatedHoursVariationContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      milestone_list: [],
      filterOption: '',
      data: {},
      tasks: [],
    };
    this.changeOption = this.changeOption.bind(this);
    this.onExit = this.onExit.bind(this);
    this.changeData = this.changeData.bind(this);
    this.copyClipboard = this.copyClipboard.bind(this);
  }

  componentDidMount() {
    this.props
      .fetchGetMilestones(this.props.projectId)
      .then((response) => {
        this.setState({
          loading: false,
          milestone_list: response.body.milestone_list,
          filterOption: '',
        });
      });
  }

  copyClipboard() {
    var dummy = document.createElement("input");
    document.body.appendChild(dummy);
    var href = `${window.location.origin}/project/${this.props.projectId}/report/estimatedhoursvariation/${this.state.filterOption}`
    dummy.setAttribute('value', href);
    dummy.select();
    document.execCommand("copy");
    document.body.removeChild(dummy);
  }

  onExit() {
    this.setState({ filterOption: '', data: false });
  }

  changeOption(e) {
    this.setState({ filterOption: e.target.value });
    this.changeData(e.target.value);
  }

  changeData(id) {
    this.props
      .fetchGetMilestonesPercentageHours(id)
      .then((response) => {
        if (response.ok) { this.setState({ data: response.body, tasks: response.body.tasks, loading: false }); }
        else {
          console.log("Bad Request");
        }
      })
  }



  render() {
    if (this.state.loading) return <CircularProgress />;
    const { data, tasks, milestone_list } = this.state;
    const { classes, selectedLanguage } = this.props;
    return (
      <div className={classes.headerContainer}>
        <Typography className={classes.title}>
          {language[selectedLanguage].tasktracking.estimatedhoursvariation}
        </Typography>
        <Button size="small" color="secondary" onClick={this.copyClipboard} className={classes.shareButton}>
          <Typography className={classes.shareText}>
            {language[this.props.selectedLanguage].share}
          </Typography>
        </Button>
        <div className={classes.line} />
        <div style={{ display: 'flex', alignItems: 'center', margin: '10px' }}>
          {milestone_list.length > 0 && (
            <Typography className={classes.text}>
              {language[this.props.selectedLanguage].averageTaskTimeChart.milestoneSelection}
            </Typography>
          )}
          <div className={classes.selectText}>
            {milestone_list.length > 0 && (
              <Select
                placeholder={language[this.props.selectedLanguage].averageTaskTimeChart.milestoneSelection}
                value={this.state.filterOption}
                onChange={this.changeOption}
              >
                <MenuItem value="none" disabled>
                  {language[this.props.selectedLanguage].averageTaskTimeChart.milestoneSelection}
                </MenuItem>
                {milestone_list.map(item => {
                  return (
                    <MenuItem key={item.milestone_id} value={item.milestone_id}>
                      {item.milestone_name}
                    </MenuItem>
                  );
                })}
              </Select>
            )}
          </div>
        </div>

        <div className={classes.line} />
        <div className={classes.wrapper}>
          {this.state.filterOption !== '' && data !== '' && tasks.length > 0 && <PercentageRange data={data} />}
          {(this.state.filterOption === '' || tasks.length === 0) && (
            <Typography style={{ textAlign: 'center' }}>
              {language[selectedLanguage].report.noReportedHoursInPeriod}.
            </Typography>
          )}
        </div>
      </div>
    );
  }
}

EstimatedHoursVariationContainer.propTypes = {
  classes: PropTypes.object.isRequired,
  projectId: PropTypes.string.isRequired,
  notifyError: PropTypes.func.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
  fetchGetMilestones: PropTypes.func.isRequired,
  filterOption: PropTypes.any.isRequired,
  fetchGetMilestonesPercentageHours: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => {
  return {
    filterOption: state.project.reporthoursFilterOption,
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      fetchGetMilestones,
      fetchGetMilestonesPercentageHours,
      notifyError,
    },
    dispatch,
  );
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withStyles(styles)(EstimatedHoursVariationContainer));
