import {
  FETCH_GET_ALL_PROJECT_BOARDS,
  FETCH_GET_ALL_BOARDS,
  FETCH_CREATE_BOARD,
  FETCH_UPDATE_BOARD,
  FETCH_DELETE_BOARD,
  FETCH_UPDATE_BOARD_ORDER,
  FETCH_REPORTED_TASKS_BETWEEN_DATES,
  FETCH_TASKS_BETWEEN_DATES,
} from '../../constants/api/boards';

export const fetchCreateBoard = body => ({
  type: FETCH_CREATE_BOARD,
  fetch: true,
  url: '/boards/',
  params: {
    method: 'POST',
    body,
  },
});

export const fetchGetProjectBoards = projectId => ({
  type: FETCH_GET_ALL_PROJECT_BOARDS,
  fetch: true,
  url: `/projects/${projectId}/boards`,
  params: {
    method: 'GET',
  },
});

export const fetchGetBoards = () => ({
  type: FETCH_GET_ALL_BOARDS,
  fetch: true,
  url: '/boards',
  params: {
    method: 'GET',
  },
});

export const fetchUpdateBoard = (id, body) => ({
  type: FETCH_UPDATE_BOARD,
  fetch: true,
  url: `/boards/${id}/`,
  params: {
    method: 'PUT',
    body,
  },
});

export const fetchDeleteBoard = id => ({
  type: FETCH_DELETE_BOARD,
  fetch: true,
  url: `/boards/${id}/`,
  params: {
    method: 'DELETE',
  },
});

export const fetchUpdateBoardOrder = (id, body) => ({
  type: FETCH_UPDATE_BOARD_ORDER,
  fetch: true,
  url: `/boards/${id}/order`,
  params: {
    method: 'PUT',
    body,
  },
});
export const fetchGetReportedTasksBoard = (id, body) => ({
  type: FETCH_REPORTED_TASKS_BETWEEN_DATES,
  fetch: true,
  url: `/boards/${id}/reported_tasks_between_dates`,
  params: {
    method: 'GET',
  },
  queryParams: {
    ...body,
  },
});
export const fetchGetTasksBoard = (id, body) => ({
  type: FETCH_TASKS_BETWEEN_DATES,
  fetch: true,
  url: `/boards/${id}/tasks_between_dates`,
  params: {
    method: 'GET',
  },
  queryParams: {
    ...body,
  },
});
