import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import Button from '@material-ui/core/Button';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import Typography from '@material-ui/core/Typography';

import 'react-day-picker/lib/style.css';

import CircularProgress from '../../../lib/CircularProgress';
import { notifyError } from '../../../../actions/notification';

import {
  fetchGetMilestones,
} from '../../../../actions/api/milestones';

import {
  fetchGetBurndown,
} from '../../../../actions/api/burncharts';

import BurnDown from './Burndown';

import language from '../../../../constants/language';

/* eslint-disable react/no-unused-state */
const styles = {
  wrapper: {
    height: '400px',
  },
  modalContent: {
    width: '500px',
    height: '400px',
  },
  title: {
    fontSize: '28px',
    fontFamiliy:
      '-apple-system, ".SFNSText-Regular", "San Francisco", BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", "Lucida Grande", Arial, sans-serif',
    color: 'rgb(68, 68, 68)',
  },
  headerContainer: {
    margin: '30px',
    width: 'auto',
  },
  text: {
    color: '#2a3d41',
    fontSize: 16,
    fontWeight: 'bold',
    marginTop: '10px',
    marginBottom: '10px',
  },
  selectText: {
    color: '#2a3d41',
    fontSize: 14,
    fontWeight: 'normal',
    marginTop: '10px',
    marginBottom: '10px',
    marginLeft: '10px',
  },
  grid: {
    display: 'flex',
    width: '100%',
  },
  container: {
    height: '700px',
  },
  modalContainer: {
    height: '700px',
  },
  line: {
    width: 'auto',
    height: '1px',
    backgroundColor: '#CCCCCC',
  },
  graph: {
    fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
  },
  shareText: {
    fontSize: 12,
    color: 'white',
    margin: '0px 0px',
  },
  shareButton: {
    margin: 5,
    padding: '0px 8px',
    borderColor: 'white',
    border: '1px solid #fff',
    backgroundColor: '#ea5941',
    '&:hover': {
      backgroundColor: '#ca3921',
    },
    color: '#fff',
    borderRadius: 0,
  },
};

class BurnDownContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      data: '',
      milestone_list: [],
      filterOption: '',
    };
    this.changeOption = this.changeOption.bind(this);
    this.changeData = this.changeData.bind(this);
    this.onExit = this.onExit.bind(this);
    this.copyClipboard = this.copyClipboard.bind(this);
  }

  componentDidMount() {
    this.props
      .fetchGetMilestones(this.props.projectId)
      .then((response) => {
        this.setState({
          loading: false,
          milestone_list: response.body.milestone_list,
          filterOption: '',
        });
      });
  }

  copyClipboard() {
    var dummy = document.createElement("input");
    document.body.appendChild(dummy);
    var href = `${window.location.origin}/project/${this.props.projectId}/report/burndown/${this.state.filterOption}`
    dummy.setAttribute('value', href);
    dummy.select();
    document.execCommand("copy");
    document.body.removeChild(dummy);
  }

  onExit() {
    this.setState({ filterOption: '' });
  }

  changeData(id) {
    this.props
      .fetchGetBurndown(id)
      .then((response) => {
        var output = {}
        output = response.body
        var estimated_hours = {}
        estimated_hours['id'] = language[this.props.selectedLanguage].burnchart.estimated_hours
        estimated_hours['data'] = response.body.hour_stats[1]['data']

        var reported_hours = {}
        reported_hours['id'] = language[this.props.selectedLanguage].burnchart.reported_hours
        reported_hours['data'] = response.body.hour_stats[0]['data']

        output['hour_stats'] = [estimated_hours, reported_hours]

        var legends = [language[this.props.selectedLanguage].burnchart.hours, language[this.props.selectedLanguage].burnchart.days]
        output['legends'] = legends

        this.setState({
          loading: false,
          data: output,
        });
      });
  }

  changeOption(e) {
    this.setState({ filterOption: e.target.value });
    this.changeData(e.target.value);
  }

  render() {
    const { loading } = this.state;
    if (loading) {
      return <CircularProgress />;
    }
    const { classes } = this.props;
    const { data, milestone_list } = this.state
    return (
      < div className={classes.container} >
        <div className={classes.headerContainer}>
          <Typography className={classes.title}>
            {language[this.props.selectedLanguage].burnchart.burndown}
          </Typography>
          <Button size="small" color="secondary" onClick={this.copyClipboard} className={classes.shareButton}>
            <Typography className={classes.shareText}>
              {language[this.props.selectedLanguage].share}
            </Typography>
          </Button>
          <div className={classes.line} />
          <div
            style={{ display: 'flex', alignItems: 'center', margin: '10px' }}
          >
            {milestone_list.length > 0 && (
              <Typography className={classes.text}>
                {language[this.props.selectedLanguage].burnchart.milestoneSelection}
              </Typography>
            )}
            <div className={classes.selectText}>
              {milestone_list.length > 0 && (
                <Select
                  value={this.state.filterOption}
                  onChange={this.changeOption}
                >
                  <MenuItem value="none" disabled>
                    {language[this.props.selectedLanguage].burnchart.milestoneSelection}
                  </MenuItem>
                  {milestone_list.map(item => {
                    return (
                      <MenuItem key={item.milestone_id} value={item.milestone_id}>
                        {item.milestone_name}
                      </MenuItem>
                    );
                  })}
                </Select>
              )}
            </div>

          </div>
        </div>
        <div style={styles.graph} className={classes.wrapper}>
          {data === '' && (
            <Typography style={{ textAlign: 'center' }}>
              {' '}
              {language[this.props.selectedLanguage].burnchart.noMilestoneSelected}{' '}
            </Typography>
          )}
          {data !== '' && data.task_number !== 0 && <BurnDown graph={data.hour_stats} leg={data.legends} />}
          {data !== '' && data.task_number === 0 && (
            <Typography style={{ textAlign: 'center' }}>
              {' '}
              {language[this.props.selectedLanguage].burnchart.noReportedHoursInMilestone}{' '}
            </Typography>
          )}
        </div>
      </div >
    );
  }
}

BurnDownContainer.propTypes = {
  classes: PropTypes.object.isRequired,
  projectId: PropTypes.string.isRequired,
  notifyError: PropTypes.func.isRequired,
  changeBurnchartMilestone: PropTypes.func.isRequired,
  fetchGetBurndown: PropTypes.func.isRequired,
  fetchGetMilestones: PropTypes.func.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};


const mapStateToProps = (state) => {
  return {
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      notifyError,
      fetchGetBurndown,
      fetchGetMilestones,
    },
    dispatch,
  );
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withStyles(styles)(BurnDownContainer));
