import React, { Component } from 'react';
import PropTypes from 'prop-types';
import equal from 'fast-deep-equal';
import CircularProgress from '../../../lib/CircularProgress';

import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';
import HighchartsMore from 'highcharts/highcharts-more';


import language from '../../../../constants/language';
import { connect } from 'react-redux';


HighchartsMore(Highcharts);

class TaskProgress extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
    };
    this.setOptions.bind(this);
  }

  componentWillMount() {
    this.setState({ loading: false });
  }

  componentDidUpdate(prevProps) {
    if (!equal(this.props.tasks, prevProps.tasks)) {
      const chartOptions = this.setOptions();
      Highcharts.chart('chart', chartOptions);
    }
    if (!equal(this.props.selectedLanguage, prevProps.selectedLanguage)) {
      const chartOptions = this.setOptions();
      Highcharts.chart('chart', chartOptions);
    }
    // if (!equal(this.props.startDate, prevProps.startDate)) {
    //   const chartOptions = this.setOptions();
    //   Highcharts.chart('chart', chartOptions);
    // }
    // if (!equal(this.props.endDate, prevProps.endDate)) {
    //   const chartOptions = this.setOptions();
    //   Highcharts.chart('chart', chartOptions);
    // }
  }


  setOptions() {
    const { tasks, series } = this.formatData();
    const { selectedLanguage } = this.props;
    return {
      chart: {
        type: 'bar'
      },
      title: {
        text: language[selectedLanguage].taskProgressChart.title
      },
      xAxis: {
        categories: tasks,
      },
      yAxis: [{
        min: 0,
        title: {
          text: language[selectedLanguage].taskProgressChart.yAxis
        }
      }],
      legend: {
        shadow: false
      },
      tooltip: {
        shared: true
      },
      plotOptions: {
        bar: {
          grouping: false,
          shadow: false,
          borderWidth: 0
        }
      },
      series: series,

    }
  }
  formatData() {
    const { selectedLanguage, tasks, users, members } = this.props;
    const newData = [];
    const copy_members = JSON.parse(JSON.stringify(members));
    const tasksNames = tasks.map((task) => { return (task['name']) });
    const tasksHours = tasks.map((task) => { return (parseFloat(task['estimated_hours'])) });
    copy_members.forEach((member) => {
      if (users[member.id]) {
        tasks.forEach((task) => {
          if (!member.tasks_reports[task.id]) {
            member.tasks_reports[task.id] = member.count;
            member.hours[member.count] = 0;
            member.count += 1;
          }
          if (users[member.id][task.id]) {
            const hours_report = parseFloat(users[member.id][task.id]);
            const index = member.tasks_reports[task.id];
            member.hours[index] = hours_report;
          }
        });
      }
    });
    newData.push({
      name: language[selectedLanguage].taskProgressChart.estimated_hours,
      data: tasksHours,
      color: '#d8d8d8',
      pointPadding: 0.2,
      pointPlacement: -0.2
    });

    copy_members.forEach((user) => {
      if (user.hours.length > 0) {
        newData.push({
          name: user['name'],
          data: user['hours'],
          pointPadding: 0.4,
          pointPlacement: -0.2,
          stacking: 'normal'
        });
      }
    });
    return ({
      'tasks': tasksNames,
      'series': newData,
    });
  }


  render() {
    if (this.state.loading) return <CircularProgress />;
    if (this.props.tasks.length > 0) {
      const chartOptions = this.setOptions();
      return (
        <div>
          <div id="chart">
            <HighchartsReact highcharts={Highcharts} options={chartOptions} />
          </div>
        </div>
      );
    }
  }
}

TaskProgress.propTypes = {
  tasks: PropTypes.array.isRequired,
  members: PropTypes.array.isRequired,
  users: PropTypes.array.isRequired,
  boardId: PropTypes.any.isRequired,
  startDate: PropTypes.any.isRequired,
  endDate: PropTypes.any.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    selectedLanguage: state.user.language,
  };
};



export default connect(
  mapStateToProps,
)(TaskProgress);