class UserContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      dataTree: [],
      repos: [],
      dataTreeCompleted: [],
      dataTreeUncompleted: [],
      tasksCompleted: [],
      tasksUnCompleted: [],
    };
    this.changeUser = this.changeUser.bind(this);
    this.changeFrom = this.changeFrom.bind(this);
    this.changeTo = this.changeTo.bind(this);
    this.changeDataTree = this.changeDataTree.bind(this);
    this.changeDataTreeCompleted = this.changeDataTreeCompleted.bind(this);
    this.changeDataTreeUncompleted = this.changeDataTreeUncompleted.bind(this);
    this.copyClipboard = this.copyClipboard.bind(this);
  }

  componentDidMount() {
    this.props.changeTrazabilityHours({
      selectedRepo: this.props.selectedRepo,
      startDate: this.props.startDate,
      endDate: this.props.endDate,
      user: this.props.userId,
    });
    this.changeDataTree(
      this.props.startDate,
      this.props.endDate,
      this.props.userId,
    );

  }

  copyClipboard() {
    var dummy = document.createElement("input");
    document.body.appendChild(dummy);
    var href = `${window.location.origin}/project/${this.props.projectId}/report/tasks/${this.props.startDate}/${this.props.endDate}/${this.props.user}`
    dummy.setAttribute('value', href);
    dummy.select();
    document.execCommand("copy");
    document.body.removeChild(dummy);
  }

  changeDataTree(startDate, endDate, userId) {
    this.setState({ dataTree: [], tasksCompleted: [], tasksUnCompleted: [], dataTreeUncompleted: [], dataTreeCompleted: [] });
    const { projectId } = this.props;
    const body = {
      start_date: startDate,
      end_date: endDate,
    };
    this.props
      .fetchVizHoursPerTask(projectId, userId, body)
      .then((response) => {
        const newdataTree = response.ok ? response.body : [];
        this.setState({ loading: false, dataTree: newdataTree });
        this.changeDataTreeCompleted(this.state.dataTree);
        this.changeDataTreeUncompleted(this.state.dataTree);
      });
  }


  changeUser(e) {
    this.props.changeTrazabilityHours({
      selectedRepo: this.props.selectedRepo,
      startDate: this.props.startDate,
      endDate: this.props.endDate,
      user: e.target.value,
    });
    this.changeDataTree(
      this.props.startDate,
      this.props.endDate,
      e.target.value,
    );
  }

  changeFrom(e) {
    const value = moment(e).format('YYYY-MM-DD');
    if (value && value <= this.props.endDate) {
      this.props.changeTrazabilityHours({
        selectedRepo: this.props.selectedRepo,
        startDate: value,
        endDate: this.props.endDate,
        user: this.props.user,
      });
      this.changeDataTree(value, this.props.endDate, this.props.user);
    } else {
      this.props.notifyError('Invalid date');
    }
  }

  changeTo(e) {
    const value = moment(e).format('YYYY-MM-DD');
    if (value && value >= this.props.startDate) {
      this.props.changeTrazabilityHours({
        selectedRepo: this.props.selectedRepo,
        startDate: this.props.startDate,
        endDate: value,
        user: this.props.user,
      });
      this.changeDataTree(this.props.startDate, value, this.props.user);
    }
    else {
      this.props.notifyError('Invalid date');
    }
  }

  changeDataTreeCompleted(dataTree) {
    if (!dataTree.children || !dataTree.children.length) {
      ;
    } else {
      let NewDataTreeCompleted = JSON.parse(JSON.stringify(dataTree));
      let all_tasks = [];
      NewDataTreeCompleted.children.forEach((board) => {
        let tasks = [];
        board.children.forEach((task) => {
          tasks.push(task);
          all_tasks.push(task);
        });
        board.children = tasks.filter(task => task.completed);
      });
      this.setState({
        dataTreeCompleted: NewDataTreeCompleted,
        tasksCompleted: all_tasks.filter(task => task.completed)
      });
    }
  }

  changeDataTreeUncompleted(dataTree) {
    if (!dataTree.children || !dataTree.children.length) {
      ;
    } else {
      let NewDataTreeUnCompleted = JSON.parse(JSON.stringify(dataTree));
      let all_tasks = [];
      NewDataTreeUnCompleted.children.forEach((board) => {
        let tasks = [];
        board.children.forEach((task) => {
          tasks.push(task);
          all_tasks.push(task);
        });
        board.children = tasks.filter(task => !task.completed);
      });
      this.setState({
        dataTreeUncompleted: NewDataTreeUnCompleted,
        tasksUnCompleted: all_tasks.filter(task => !task.completed)
      });
    }
  }

  render() {
    if (this.state.loading) return <CircularProgress />;
    const { classes, users, selectedLanguage } = this.props;
    const { dataTreeCompleted, dataTreeUncompleted, tasksUnCompleted, tasksCompleted } = this.state;
    const langDict = language[selectedLanguage].report;
    return (
      <div className={classes.headerContainer}>
        <Typography className={classes.title}>
          {langDict.tasks}
        </Typography>
        <Button size="small" color="secondary" onClick={this.copyClipboard} className={classes.shareButton}>
          <Typography className={classes.shareText}>
            {language[this.props.selectedLanguage].share}
          </Typography>
        </Button>
        <div className={classes.line} />
        <div className={classes.grid}>
          <div
            style={{ display: 'flex', alignSelf: 'center', marginLeft: '10px' }}
          >
            <Typography className={classes.text}>{langDict.user}:</Typography>
            <Select value={this.props.user} onChange={this.changeUser}>
              {users.map((u) => {
                return (
                  <MenuItem key={u.id} value={u.id}>
                    {truncateString(u.name, 30)}
                  </MenuItem>
                );
              })}
            </Select>
          </div>
          <div
            style={{ display: 'flex', alignSelf: 'center', marginLeft: '10px' }}
          >
            <Typography className={classes.text}>{langDict.from}:</Typography>
            <div className={classes.selectText}>
              <DayPickerInput
                value={this.props.startDate}
                onDayChange={this.changeFrom}
                dayPickerProps={{
                  disabledDays: {
                    after: moment().toDate(),
                  },
                }}
              />
            </div>
          </div>
          <div
            style={{ display: 'flex', alignSelf: 'center', marginLeft: '10px' }}
          >
            <Typography className={classes.text}>{langDict.to}:</Typography>
            <div className={classes.selectText}>
              <DayPickerInput
                value={this.props.endDate}
                onDayChange={this.changeTo}
                dayPickerProps={{
                  disabledDays: {
                    before: moment(this.props.startDate).toDate(),
                    after: moment().toDate(),
                  },
                }}
              />
            </div>
          </div>
        </div>
        <div className={classes.line} />
        <div className={classes.container}>
          <Typography className={classes.item2}>{langDict.completetasks}</Typography>
          <Typography className={classes.item2}>{langDict.notcompletetasks}</Typography>
          <div className={classes.item3}>
            <div className={classes.container}>
            </div>
            {tasksCompleted.length > 0 &&
              <TreeMap
                data={dataTreeCompleted}
                params={[this.props.startDate, this.props.endDate]}
              />}
            {tasksCompleted.length === 0 && (
              <Typography className={classes.noDataMsg}>
                {' '}
                {langDict.nocompletetasks}.{' '}
              </Typography>
            )}

          </div>

          <div className={classes.item3}>
            <div className={classes.container}>
            </div>
            {tasksUnCompleted.length === 0 && (
              <Typography className={classes.noDataMsg}>
                {' '}
                {langDict.nonotcompletetasks}.{' '}
              </Typography>
            )}
            {tasksUnCompleted.length > 0 && (
              <TreeMap
                data={dataTreeUncompleted}
                params={[this.props.startDate, this.props.endDate]}
              />
            )}
          </div>
        </div>
      </div>
    );
  }
}

UserContainer.propTypes = {
  classes: PropTypes.object.isRequired,
  projectId: PropTypes.string.isRequired,
  users: PropTypes.array.isRequired,
  userId: PropTypes.string.isRequired,
  fetchVizHoursPerTask: PropTypes.func.isRequired,
  changeTrazabilityHours: PropTypes.func.isRequired,
  notifyError: PropTypes.func.isRequired,
  selectedRepo: PropTypes.any.isRequired,
  user: PropTypes.any.isRequired,
  endDate: PropTypes.any.isRequired,
  startDate: PropTypes.any.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    userId: state.auth.id,
    selectedRepo: state.project.trazabilityhoursRepository,
    user: state.project.trazabilityhoursUser,
    endDate: state.project.trazabilityhoursEndDate,
    startDate: state.project.trazabilityhoursStarDate,
    users: allMembers(ownProps, state),
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      fetchVizHoursPerTask,
      changeTrazabilityHours,
      notifyError,
    },
    dispatch,
  );
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withStyles(styles)(UserContainer));
