import React, { Fragment, Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import moment from 'moment';

import Typography from '@material-ui/core/Typography';
import Tooltip from '@material-ui/core/Tooltip';
import Button from '@material-ui/core/Button';

import DayPickerInput from 'react-day-picker/DayPickerInput';
import 'react-day-picker/lib/style.css';
import CircularProgress from '../../../lib/CircularProgress';

import { fetchVizHoursPerUser } from '../../../../actions/api/viz';
import { notifyError } from '../../../../actions/notification';
import { allMembers } from '../../../../selectors/members';
import BlankProfile from '../../../../assets/blankprofile.png';
import { changeUsersHours } from '../../../../actions/project';

import DynamicUsersChart from './DynamicUsersChart';
import language from '../../../../constants/language';
/* eslint-disable max-len */
const styles = {
  wrapper: {
    height: '400px',
    width: 'auto',
  },
  modalContent: {
    width: '600px',
    height: '400px',
  },
  modalContainer: {
    // width: '800px',
  },
  title: {
    fontSize: '28px',
    fontFamiliy:
      '-apple-system, ".SFNSText-Regular", "San Francisco", BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", "Lucida Grande", Arial, sans-serif',
    color: 'rgb(68, 68, 68)',
  },
  headerContainer: {
    margin: '30px',
    width: 'auto',
  },
  text: {
    color: '#2a3d41',
    fontSize: 16,
    fontWeight: 'bold',
    marginTop: '10px',
    marginBottom: '10px',
  },
  selectText: {
    color: '#2a3d41',
    fontSize: 14,
    fontWeight: 'normal',
    marginTop: '10px',
    marginBottom: '10px',
    marginLeft: '10px',
  },
  grid: {
    display: 'flex',
    width: '100%',
    margin: '10px',
  },
  line: {
    width: 'auto',
    height: '1px',
    backgroundColor: '#CCCCCC',
  },
  info: {
    color: '#2a3d41',
    fontSize: 16,
    fontWeight: 'bold',
    marginTop: '5px',
  },
  box: {
    display: 'flex',
    marginRight: '10px',
    padding: '5px',
    alignItems: 'center',
  },
  customIcon: {
    height: '24px',
    width: '24px',
    display: 'block',
    'border-radius': '50%',
    marginRight: '5px',
  },
  shareText: {
    fontSize: 12,
    color: 'white',
    margin: '0px 0px',
  },
  shareButton: {
    margin: 5,
    padding: '0px 8px',
    borderColor: 'white',
    border: '1px solid #fff',
    backgroundColor: '#ea5941',
    '&:hover': {
      backgroundColor: '#ca3921',
    },
    color: '#fff',
    borderRadius: 0,
  },
};

class UserContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      data: null,
      labels: null,
      totalHours: null,
    };
    this.changeFrom = this.changeFrom.bind(this);
    this.changeTo = this.changeTo.bind(this);
    this.changeData = this.changeData.bind(this);
    this.copyClipboard = this.copyClipboard.bind(this);
  }

  componentWillMount() {
    this.changeData(this.props.startDate, this.props.endDate);
  }

  copyClipboard() {
    var dummy = document.createElement("input");
    document.body.appendChild(dummy);
    var href = `${window.location.origin}/project/${this.props.projectId}/report/user/${this.props.startDate}/${this.props.endDate}`
    dummy.setAttribute('value', href); //MODIFICAR
    dummy.select();
    document.execCommand("copy");
    document.body.removeChild(dummy);
  }

  changeData(startDate, endDate) {
    this.props
      .fetchVizHoursPerUser(this.props.projectId, {
        start_date: startDate,
        end_date: endDate,
      })
      .then((response) => {
        if (response.ok) {
          const newTotalHours = {};
          response.body.data.forEach((user) => {
            newTotalHours[user.user_email] = 0;
            Object.keys(user).forEach((board) => {
              if (response.body.boards.includes(board)) {
                newTotalHours[user.user_email] += user[board];
              }
            });
          });
          this.setState({
            loading: false,
            data: response.body.data,
            labels: response.body.boards,
            totalHours: newTotalHours,
          });
        }
      });
  }

  changeFrom(e) {
    const value = moment(e).format('YYYY-MM-DD');

    if (value && value <= this.props.endDate) {
      this.props.changeUsersHours({
        startDate: value,
        endDate: this.props.endDate,
      });
      this.changeData(value, this.props.endDate);
    } else {
      this.props.notifyError('Invalid date');
    }
  }

  changeTo(e) {
    const value = moment(e).format('YYYY-MM-DD');

    if (value && value >= this.props.startDate) {
      this.props.changeUsersHours({
        startDate: this.props.startDate,
        endDate: value,
      });
      this.changeData(this.props.startDate, value);
    } else {
      this.props.notifyError('Invalid date');
    }
  }



  render() {
    const { classes, members } = this.props;
    if (this.state.loading) return <CircularProgress />;
    const {
      data,
      labels,
      totalHours,
    } = this.state;
    return (
      <div className={classes.headerContainer}>
        <Typography className={classes.title}>
          {language[this.props.selectedLanguage].usersChartIndex.hoursChart}
        </Typography>
        <Button size="small" color="secondary" onClick={this.copyClipboard} className={classes.shareButton}>
          <Typography className={classes.shareText}>
            {language[this.props.selectedLanguage].share}
          </Typography>
        </Button>
        <div className={classes.line} />
        <div className={classes.grid}>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
              marginLeft: '10px',
            }}
          >
            <Typography className={classes.text}>
              {language[this.props.selectedLanguage].usersChartIndex.from}{' '}
            </Typography>
            <DayPickerInput
              value={this.props.startDate}
              onDayChange={this.changeFrom}
              dayPickerProps={{
                disabledDays: {
                  after: moment().toDate(),
                },
              }}
            />
          </div>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
              marginLeft: '10px',
            }}
          >
            <Typography className={classes.text}>
              {language[this.props.selectedLanguage].usersChartIndex.to}{' '}
            </Typography>
            <DayPickerInput
              value={this.props.endDate}
              onDayChange={this.changeTo}
              dayPickerProps={{
                disabledDays: {
                  before: moment(this.props.startDate).toDate(),
                  after: moment().toDate(),
                },
              }}
            />
          </div>
        </div>
        <div className={classes.line} />
        {Object.keys(totalHours).length > 0 && (
          <Fragment>
            <Typography className={classes.text}>
              {language[this.props.selectedLanguage].usersChartIndex.totalHours}
              :{' '}
            </Typography>
            <div style={{ display: 'flex' }}>
              {members.map((user) => {
                return (
                  <div className={classes.box} key={user.email}>
                    <Tooltip placement="top" title={user.name}>
                      <div>
                        {(!user.image || user.image === 'null') && (
                          <img
                            alt=""
                            className={classes.customIcon}
                            src={BlankProfile}
                          />
                        )}
                        {user.image && user.image !== 'null' && (
                          <img
                            alt=""
                            className={classes.customIcon}
                            src={user.image}
                          />
                        )}
                      </div>
                    </Tooltip>
                    <div className={classes.info}>
                      <Typography
                        className={classes.text}
                        style={{ marginTop: 0, marginBottom: 0 }}
                      >
                        {totalHours[user.email]}
                      </Typography>
                    </div>
                  </div>
                );
              })}
            </div>
          </Fragment>
        )}
        <div className={classes.wrapper}>
          {data.length > 0 && (
            <DynamicUsersChart
              data={data}
              labels={labels}
              params={[this.props.startDate, this.props.endDate]}
            />
          )}
          {!data.length && (
            <Typography style={{ textAlign: 'center' }}>
              {language[this.props.selectedLanguage].usersChartIndex.noReported}
            </Typography>
          )}
        </div>
      </div>
    );
  }
}

UserContainer.propTypes = {
  classes: PropTypes.object.isRequired,
  projectId: PropTypes.string.isRequired,
  fetchVizHoursPerUser: PropTypes.func.isRequired,
  notifyError: PropTypes.func.isRequired,
  members: PropTypes.array.isRequired,
  changeUsersHours: PropTypes.func.isRequired,
  endDate: PropTypes.any.isRequired,
  startDate: PropTypes.any.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    members: allMembers(ownProps, state),
    endDate: state.project.userhoursEndDate,
    startDate: state.project.userhoursStarDate,
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      fetchVizHoursPerUser,
      notifyError,
      changeUsersHours,
    },
    dispatch,
  );
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withStyles(styles)(UserContainer));
