import React, { Component } from 'react';
import PropTypes from 'prop-types';
import equal from 'fast-deep-equal';
import CircularProgress from '../../../lib/CircularProgress';
import { bindActionCreators } from 'redux';

import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';
import HighchartsMore from 'highcharts/highcharts-more';

import { fetchGetHours } from '../../../../actions/api/hours';


import language from '../../../../constants/language';
import { connect } from 'react-redux';


HighchartsMore(Highcharts);

class WorkLoad extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
    };
    this.setOptions.bind(this);
  }

  componentDidMount() {
    this.setState({ loading: false});
  }

  componentDidUpdate(prevProps) {
    if (!equal(this.props.tasks, prevProps.tasks)) {
      const chartOptions = this.setOptions();
      Highcharts.chart('chart', chartOptions);
    }
    if (!equal(this.props.selectedLanguage, prevProps.selectedLanguage)) {
      const chartOptions = this.setOptions();
      Highcharts.chart('chart', chartOptions);
    }
  }


  setOptions() {
    const {names, tasks} = this.formatData();
    const { selectedLanguage } = this.props;
    return {
        chart: {
          type: 'bar'
        },
        title: {
          text: language[selectedLanguage].WorkLoadChart.title
        },
        xAxis: {
          categories: names,
        },
        yAxis: [{
          min: 0,
          title: {
            text: language[selectedLanguage].WorkLoadChart.yAxis
          }  
        }],
        legend: {
          shadow: false
        },
        tooltip: {
          shared: true
        },
        plotOptions: {
          bar: {
            grouping: false,
            shadow: false,
            borderWidth: 0
          }
        },
        series: tasks,

    }
  }
  formatData() {
    const { selectedLanguage, tasks, members} = this.props;
    const newData = [];
    const userAssigned = {};
    const userNames = [];
    const total_estimated_hours = {};
    const new_tasks = [];
    tasks.forEach((task) => {
      const assigned = task.assigned;
      if (assigned.length > 0){
        const dict = {};
        dict['estimated_hours'] = task.estimated_hours;
        dict['name'] = task.name;
        dict['hours'] = [];
        dict['users'] = [];
        assigned.forEach((user_id)=>{
          if (!userAssigned[user_id]){
            members.forEach((member)=>{
              if (member.id === user_id){
                userAssigned[user_id] = member.name;
                userNames.push(member.name);
              }
            });  
          }
          dict['users'].push(user_id);
        });
        new_tasks.push(dict);
      }
    });
    const userIds =  Array.from((Object.keys(userAssigned)));
    new_tasks.forEach((task) =>{
      userIds.forEach((user) =>{
        var value = 0;
        if (task['users'].indexOf(user) > -1){
          value = parseFloat(task.estimated_hours);
        }
        task['hours'].push(value); 
        if (!total_estimated_hours[user]){
          total_estimated_hours[user] = 0;
        }
        total_estimated_hours[user] += value;
        
      });
    });
    newData.push({
      name: language[selectedLanguage].WorkLoadChart.estimated_hours,
      data: Array.from(Object.values(total_estimated_hours)),
      color: 	'#d8d8d8',
      pointPadding: 0.2,
      pointPlacement: -0.2

    });
    new_tasks.forEach((task) => {
      newData.push({
        name: task['name'],
        data: task['hours'],
        pointPadding: 0.2,
        pointPlacement: -0.2,
        stacking: 'normal'
      }); 
    });
    
    return ({
      'names': userNames,
      'tasks': newData,
    });
  }

  render() {
    if (this.state.loading) return <CircularProgress />;
    if (this.props.tasks.length > 0){
      const chartOptions = this.setOptions();
      return (
        <div>
          <div id="chart">
            <HighchartsReact highcharts={Highcharts} options={chartOptions} />
          </div>
        </div>
      );
    }
  }
}

WorkLoad.propTypes = {
  tasks: PropTypes.array.isRequired,
  members: PropTypes.array.isRequired,
  boardId: PropTypes.string.isRequired,
  fetchGetHours: PropTypes.func.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
  endDate: PropTypes.any.isRequired,
  startDate: PropTypes.any.isRequired,
};

const mapStateToProps = (state) => {
  return {
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({ fetchGetHours }, dispatch);
};


export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(WorkLoad);