import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import moment from 'moment';

import Button from '@material-ui/core/Button';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import Typography from '@material-ui/core/Typography';

import DayPickerInput from 'react-day-picker/DayPickerInput';
import 'react-day-picker/lib/style.css';

import CircularProgress from '../../../lib/CircularProgress';
import { notifyError } from '../../../../actions/notification';

import {
  fetchVizHoursPerBoard,
  fetchVizHoursPerLabel,
} from '../../../../actions/api/viz';
import { changeReportHours } from '../../../../actions/project';

import DynamicChart from './DynamicHours';
import language from '../../../../constants/language';

/* eslint-disable react/no-unused-state */
const styles = {
  wrapper: {
    height: '400px',
  },
  modalContent: {
    width: '500px',
    height: '400px',
  },
  title: {
    fontSize: '28px',
    fontFamiliy:
      '-apple-system, ".SFNSText-Regular", "San Francisco", BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", "Lucida Grande", Arial, sans-serif',
    color: 'rgb(68, 68, 68)',
  },
  headerContainer: {
    margin: '30px',
    width: 'auto',
  },
  text: {
    color: '#2a3d41',
    fontSize: 16,
    fontWeight: 'bold',
    marginTop: '10px',
    marginBottom: '10px',
  },
  selectText: {
    color: '#2a3d41',
    fontSize: 14,
    fontWeight: 'normal',
    marginTop: '10px',
    marginBottom: '10px',
    marginLeft: '10px',
  },
  grid: {
    display: 'flex',
    width: '100%',
  },
  container: {
    height: '700px',
  },
  modalContainer: {
    height: '700px',
  },
  line: {
    width: 'auto',
    height: '1px',
    backgroundColor: '#CCCCCC',
  },
  shareText: {
    fontSize: 12,
    color: 'white',
    margin: '0px 0px',
  },
  shareButton: {
    margin: 5,
    padding: '0px 8px',
    borderColor: 'white',
    border: '1px solid #fff',
    backgroundColor: '#ea5941',
    '&:hover': {
      backgroundColor: '#ca3921',
    },
    color: '#fff',
    borderRadius: 0,
  },
};

class HoursContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      data: null,
      labels: null,
      total: null,
      selected: null,
      selectedIndex: null,
    };
    this.onClickHours = this.onClickHours.bind(this);
    this.changeOption = this.changeOption.bind(this);
    this.changeFrom = this.changeFrom.bind(this);
    this.changeTo = this.changeTo.bind(this);
    this.changeData = this.changeData.bind(this);
    this.onExit = this.onExit.bind(this);
    this.copyClipboard = this.copyClipboard.bind(this);
  }

  componentWillMount() {
    this.changeData(
      this.props.filterOption,
      this.props.startDate,
      this.props.endDate,
    );
  }

  copyClipboard() {
    var dummy = document.createElement("input");
    document.body.appendChild(dummy);
    var href = `${window.location.origin}/project/${this.props.projectId}/report/hours/${this.props.startDate}/${this.props.endDate}/${this.props.filterOption}`
    dummy.setAttribute('value', href);
    dummy.select();
    document.execCommand("copy");
    document.body.removeChild(dummy);
  }

  onClickHours(d) {
    if (this.state.selected) {
      this.setState({ selected: null, selectedIndex: null });
    } else {
      const { labels } = this.state;
      const index = Object.values(labels).indexOf(d.id);
      const selectedId = Object.keys(labels)[index];
      this.setState({ selected: selectedId, selectedIndex: d.index });
    }
  }

  onExit() {
    this.setState({ selected: null, selectedIndex: null });
  }

  changeData(filterOption, startDate, endDate) {
    this.props
      .fetchVizHoursPerBoard(this.props.projectId, {
        start_date: startDate,
        end_date: endDate,
        group_by: filterOption,
      })
      .then((response) => {
        if (response.ok) {
          const getValue = (v) => {
            const newV = v < 1 ? 0 : v;
            return newV;
          };

          let summedData = 0;
          let max = 0;
          let tmp = 0;
          const keys = Object.values(response.body.boards);
          response.body.data.forEach((elem) => {
            keys.forEach((key) => {
              summedData += getValue(elem[key]);
              tmp += getValue(elem[key]);
            });
            if (tmp > max) {
              max = tmp;
            }
            tmp = 0;
          });
          this.setState({
            loading: false,
            data: response.body.data,
            labels: response.body.boards,
            total: summedData,
          });
        }
      });
  }

  changeOption(e) {
    const filterOption = e.target.value;
    const { startDate } = this.props;
    let blockedDays;
    switch (filterOption) {
      case 'day':
        blockedDays = moment(startDate, 'YYYY-MM-DD')
          .add(30, 'days')
          .startOf('day');
        break;
      case 'week':
        blockedDays = moment(startDate, 'YYYY-MM-DD')
          .add(2, 'months')
          .startOf('day');
        break;
      case 'month':
        blockedDays = moment(startDate, 'YYYY-MM-DD')
          .add(1, 'years')
          .startOf('day');
        break;
      case 'year':
        blockedDays = moment(startDate, 'YYYY-MM-DD')
          .add(6, 'years')
          .startOf('day');
        break;
      default:
        blockedDays = moment(startDate, 'YYYY-MM-DD')
          .add(30, 'days')
          .startOf('day');
    }
    if (blockedDays.isAfter(moment(), 'day')) {
      blockedDays = moment();
    }
    const endDate = blockedDays.format('YYYY-MM-DD');
    this.props.changeReportHours({
      endDate,
      startDate: this.props.startDate,
      filterOption: e.target.value,
    });
    this.setState({ filterOption: e.target.value });
    this.changeData(e.target.value, this.props.startDate, endDate);
  }

  changeFrom(e) {
    const value = moment(e).format('YYYY-MM-DD');
    const { filterOption } = this.props;
    let blockedDays;
    switch (filterOption) {
      case 'day':
        blockedDays = moment(value, 'YYYY-MM-DD')
          .add(30, 'days')
          .format('YYYY-MM-DD');

        break;
      case 'week':
        blockedDays = moment(value, 'YYYY-MM-DD')
          .add(2, 'months')
          .format('YYYY-MM-DD');
        break;
      case 'month':
        blockedDays = moment(value, 'YYYY-MM-DD')
          .add(1, 'years')
          .format('YYYY-MM-DD');
        break;
      case 'year':
        blockedDays = moment(value, 'YYYY-MM-DD')
          .add(6, 'years')
          .format('YYYY-MM-DD');
        break;
      default:
        blockedDays = moment(value, 'YYYY-MM-DD')
          .add(30, 'days')
          .format('YYYY-MM-DD');
    }
    if (moment(blockedDays, 'YYYY-MM-DD').isAfter(moment(), 'day')) {
      blockedDays = moment().format('YYYY-MM-DD');
    }
    if (value) {
      this.props.changeReportHours({
        endDate: blockedDays,
        startDate: value,
        filterOption: this.props.filterOption,
      });
      this.setState({
        startDate: value,
        endDate: blockedDays,
      });
      this.changeData(
        this.props.filterOption,
        value,
        blockedDays,
      );
    } else {
      this.props.notifyError('Invalid date');
    }
  }

  changeTo(e) {
    const value = moment(e).format('YYYY-MM-DD');
    const { startDate, filterOption } = this.props;
    let blockedDays;
    switch (filterOption) {
      case 'day':
        blockedDays = moment(startDate, 'YYYY-MM-DD')
          .add(30, 'days')
          .format('YYYY-MM-DD');
        break;
      case 'week':
        blockedDays = moment(startDate, 'YYYY-MM-DD')
          .add(2, 'months')
          .format('YYYY-MM-DD');
        break;
      case 'month':
        blockedDays = moment(startDate, 'YYYY-MM-DD')
          .add(1, 'years')
          .format('YYYY-MM-DD');
        break;
      case 'year':
        blockedDays = moment(startDate, 'YYYY-MM-DD')
          .add(6, 'years')
          .format('YYYY-MM-DD');
        break;
      default:
        blockedDays = moment(startDate, 'YYYY-MM-DD')
          .add(30, 'days')
          .format('YYYY-MM-DD');
    }
    if (moment(blockedDays, 'YYYY-MM-DD').isAfter(moment(), 'day')) {
      blockedDays = moment().format('YYYY-MM-DD');
    }
    if (value) {
      if (moment(value, 'YYYY-MM-DD').isAfter(moment(blockedDays, 'YYYY-MM-DD'))) {
        this.props.changeReportHours({
          endDate: blockedDays,
          startDate: this.props.startDate,
          filterOption: this.props.filterOption,
        });
        this.changeData(this.props.filterOption, this.props.startDate, value);
      } else {
        this.props.changeReportHours({
          endDate: value,
          startDate: this.props.startDate,
          filterOption: this.props.filterOption,
        });
        this.changeData(this.props.filterOption, this.props.startDate, value);
      }
    } else {
      this.props.notifyError('Invalid date');
    }
  }

  render() {
    if (this.state.loading) return <CircularProgress />;
    const {
      data, labels, total,
    } = this.state;
    const { classes, filterOption, startDate } = this.props;
    let blockedDays;
    let maxPeriod;
    switch (filterOption) {
      case 'day':
        blockedDays = moment(startDate, 'YYYY-MM-DD')
          .add(30, 'days')
          .startOf('day');
        maxPeriod = language[this.props.selectedLanguage].hoursIndex.maxFilterDay;
        break;
      case 'week':
        blockedDays = moment(startDate, 'YYYY-MM-DD')
          .add(2, 'months')
          .startOf('day');
        maxPeriod = language[this.props.selectedLanguage].hoursIndex.maxFilterWeek;
        break;
      case 'month':
        blockedDays = moment(startDate, 'YYYY-MM-DD')
          .add(1, 'years')
          .startOf('day');
        maxPeriod = language[this.props.selectedLanguage].hoursIndex.maxFilterMonth;
        break;
      case 'year':
        blockedDays = moment(startDate, 'YYYY-MM-DD')
          .add(6, 'years')
          .startOf('day');
        maxPeriod = language[this.props.selectedLanguage].hoursIndex.maxFilterYear;
        break;
      default:
        blockedDays = moment(startDate, 'YYYY-MM-DD')
          .add(30, 'days')
          .startOf('day');
        maxPeriod = language[this.props.selectedLanguage].hoursIndex.maxFilterDay;
    }
    if (blockedDays.isAfter(moment(), 'day')) {
      blockedDays = moment();
    }
    return (
      <div className={classes.container}>
        <div className={classes.headerContainer}>
          <Typography className={classes.title}>
            {language[this.props.selectedLanguage].hoursIndex.totalHours}
          </Typography>
          <Button size="small" color="secondary" onClick={this.copyClipboard} className={classes.shareButton}>
            <Typography className={classes.shareText}>
              {language[this.props.selectedLanguage].share}
            </Typography>
          </Button>
          <div className={classes.line} />
          <div className={classes.grid}>
            <div
              style={{
                display: 'flex',
                alignItems: 'center',
                marginLeft: '10px',
              }}
            >
              <Typography className={classes.text}>
                {language[this.props.selectedLanguage].hoursIndex.from}
              </Typography>
              <div className={classes.selectText}>
                <DayPickerInput
                  value={this.props.startDate}
                  onDayChange={this.changeFrom}
                  dayPickerProps={{
                    disabledDays: {
                      after: moment().toDate(),
                    },
                  }}
                />
              </div>
            </div>
            <div
              style={{
                display: 'flex',
                alignItems: 'center',
                marginLeft: '10px',
              }}
            >
              <Typography className={classes.text}>
                {language[this.props.selectedLanguage].hoursIndex.to}
              </Typography>
              <div className={classes.selectText}>
                <DayPickerInput
                  value={this.props.endDate}
                  onDayChange={this.changeTo}
                  dayPickerProps={{
                    disabledDays: {
                      before: moment(this.props.startDate).toDate(),
                      after: blockedDays.toDate(),
                    },
                  }}
                />
              </div>
            </div>
          </div>
          <div
            style={{ display: 'flex', alignItems: 'center', margin: '10px' }}
          >
            <Typography className={classes.text}>
              {language[this.props.selectedLanguage].hoursIndex.filterBy}
            </Typography>
            <div className={classes.selectText}>
              <Select
                value={this.props.filterOption}
                onChange={this.changeOption}
              >
                <MenuItem value="day"> {language[this.props.selectedLanguage].hoursIndex.day} </MenuItem>
                <MenuItem value="week"> {language[this.props.selectedLanguage].hoursIndex.week} </MenuItem>
                <MenuItem value="month"> {language[this.props.selectedLanguage].hoursIndex.month} </MenuItem>
                <MenuItem value="year"> {language[this.props.selectedLanguage].hoursIndex.year} </MenuItem>
              </Select>
            </div>
            <Typography>
              {maxPeriod}
            </Typography>
          </div>
          <div className={classes.line} />
          {total > 0 && (
            <div style={{ display: 'flex' }}>
              <Typography className={classes.text}>
                {language[this.props.selectedLanguage].hoursIndex.totalHours}
                &nbsp;
              </Typography>
              <Typography className={classes.text}>{Math.round(total * 100) / 100}</Typography>
            </div>
          )}
        </div>
        <div className={classes.wrapper}>
          {data.length > 0 && (
            <DynamicChart
              data={data}
              labels={labels}
              params={[filterOption, this.props.startDate, this.props.endDate]}
            />
          )}
          {!data.length && (
            <Typography style={{ textAlign: 'center' }}>
              {' '}
              {language[this.props.selectedLanguage].hoursIndex.noReported}{' '}
            </Typography>
          )}
        </div>
      </div>
    );
  }
}

HoursContainer.propTypes = {
  classes: PropTypes.object.isRequired,
  projectId: PropTypes.string.isRequired,
  fetchVizHoursPerBoard: PropTypes.func.isRequired,
  notifyError: PropTypes.func.isRequired,
  filterOption: PropTypes.any.isRequired,
  endDate: PropTypes.any.isRequired,
  startDate: PropTypes.any.isRequired,
  changeReportHours: PropTypes.func.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    filterOption: state.project.reporthoursFilterOption,
    endDate: state.project.reporthoursEndDate,
    startDate: state.project.reporthoursStartDate,
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      fetchVizHoursPerBoard,
      fetchVizHoursPerLabel,
      changeReportHours,
      notifyError,
    },
    dispatch,
  );
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withStyles(styles)(HoursContainer));
