import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import Highcharts from 'highcharts';
import equal from 'fast-deep-equal';
import HighchartsReact from 'highcharts-react-official';
import { fetchVizHoursPerLabel } from '../../../../actions/api/viz';
import CircularProgress from '../../../lib/CircularProgress';
import language from '../../../../constants/language';

class AverageBoardHours extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
    };
    this.setOptions.bind(this);
  }

  componentWillMount() {
    this.setState({ loading: false });
  }

  componentDidUpdate(prevProps) {
    if (!equal(this.props.data, prevProps.data)) {
      const chartOptions = this.setOptions();
      Highcharts.chart('chart', chartOptions);
    }
    if (!equal(this.props.selectedLanguage, prevProps.selectedLanguage)) {
      const chartOptions = this.setOptions();
      Highcharts.chart('chart', chartOptions);
    }
  }

  setOptions() {
    const {boardNames , averageHours, totalTasks} = this.formatData();

    const { selectedLanguage } = this.props;
    return {
      chart: {
        //type: 'column',
        height: 350,
        zoomType: 'xy',
      },
      credits: false,
      title: {
        text: language[selectedLanguage].averageTaskTimeChart.boardAverageHours,
      },
      xAxis: {
        categories: boardNames,
      },
      yAxis: [{ // Primary yAxis
        labels: {
          format: '{value} ' + language[selectedLanguage].averageTaskTimeChart.hours,
        },
        title: {
          text: language[selectedLanguage].averageTaskTimeChart.averageHours,
        },
        min: 0,
      }, { // Secondary yAxis
        gridLineWidth: 0,
        title: {
          text: language[selectedLanguage].averageTaskTimeChart.totalCompletedTasks,
          style: {
            //color: Highcharts.getOptions().colors[0]
          }
        },
        labels: {
          style: {
            //color: Highcharts.getOptions().colors[0]
          }
        },
        opposite: true,
      }],
      series: [
        {
          name: language[selectedLanguage].averageTaskTimeChart.averageHours,
          type: 'column',
          yAxis: 0,
          // color: '',
          data: averageHours,
          // colorByPoint: true,
          tooltip: {
            //valueSuffix: 'Horas',
          },
        }, {
          name: language[selectedLanguage].averageTaskTimeChart.totalCompletedTasks,
          type: 'spline',
          color: 'black',
          yAxis: 1,
          data: totalTasks,
        },
      ],
      tooltip: {
        shared: true,
        disabled: true,
        backgroundColor: '#616161',
        borderColor: '#616161',
        borderWidth: 1,
        style: {
          color: 'white',
          fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
          textAlign: 'center',
        },
        formatter() {
          return `${language[selectedLanguage].averageTaskTimeChart.averageHours}: ${this.y} 
           <br>
           ${language[selectedLanguage].averageTaskTimeChart.totalTasks}: ${this.points[1].y}`;
        },
      },
      plotOptions: {
        series: {
          dataLabels: {
            enabled: false,
            // format: `{point.y:.1f} ${language[selectedLanguage].averageTaskTimeChart.hours}`,
          },
          events: {
            legendItemClick: function(e) {
              e.preventDefault();
            },
          },
        },
      },
    };
  }

  formatData() {
    const boards = this.props.labels;
    const boardIDs = Array.from(new Set(Object.keys(boards)));

    const { data } = this.props;
    const averageHours = [];
    const totalTasks = [];
    const boardNames = [];
    boardIDs.forEach((ID) => {
      if(data.hours_info[ID]){
        averageHours.push({
          name: boards[ID],
          y: parseInt(data.hours_info[ID]["average_estimated_hours"]),
        });
        totalTasks.push({
          name: boards[ID],
          y: data.hours_info[ID]["task_count"],
        });
        boardNames.push(boards[ID]);
      };
    });
    return {
      'boardNames': boardNames,
      'averageHours': averageHours,
      'totalTasks': totalTasks,
    };
  }

  render() {
    if (this.state.loading) return <CircularProgress />;
    const chartOptions = this.setOptions();
    return (
      <div>
        <div id="chart">
          <HighchartsReact highcharts={Highcharts} options={chartOptions} />
        </div>
      </div>
    );
  }
}

AverageBoardHours.propTypes = {
  data: PropTypes.object.isRequired,
  labels: PropTypes.object.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({ fetchVizHoursPerLabel }, dispatch);
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AverageBoardHours);
