import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';

import 'react-day-picker/lib/style.css';
import CircularProgress from '../../../lib/CircularProgress';

import { fetchVizHoursPerUser } from '../../../../actions/api/viz';
import { notifyError } from '../../../../actions/notification';
import { allMembers } from '../../../../selectors/members';
import { changeUsersHours } from '../../../../actions/project';

import User from '../User';
import Trazability from '../Trazability';
import Timeline from '../Timeline';
import BurnUp from '../BurnUp';
import BurnDown from '../BurnDown';
import PR from '../PR';
import Lead from '../Lead';
import Github from '../Github';
import AverageTaskCompletionTime from '../AverageTaskCompletionTime';
import EstimatedHoursVariation from '../EstimatedHoursVariation';
