import Graph5 from '../../../../assets/report_graph5.svg';
import Graph6 from '../../../../assets/report_graph6.svg';
import Graph7 from '../../../../assets/report_graph7.svg';
import Graph8 from '../../../../assets/report_graph8.svg';
import Graph9 from '../../../../assets/report_graph9.svg';
