import language from '../../../../constants/language';
/* eslint-disable max-len */
const styles = {

  title: {
    fontSize: '28px',
    fontFamiliy:
      '-apple-system, ".SFNSText-Regular", "San Francisco", BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", "Lucida Grande", Arial, sans-serif',
    color: 'rgb(68, 68, 68)',
  },
  cardTitle: {
    fontSize: '16px',
    fontFamily: "Roboto, Helvetica, Arial, sans-serif",
    color: 'rgb(68, 68, 68)',
    fontWeight: 'bold',
  },
  headerContainer: {
    margin: '30px',
    width: 'auto',
  },
  text: {
    color: '#2a3d41',
    fontFamily: "Roboto, Helvetica, Arial, sans-serif",
    fontWeight: 'normal',
    fontSize: 16,
    marginTop: '10px',
    marginBottom: '10px',
  },
  grid: {
    display: 'flex',
    width: '100%',
    margin: '10px',
  },
  line: {
    width: 'auto',
    height: '1px',
    backgroundColor: '#CCCCCC',
  },
  info: {
    color: '#2a3d41',
    fontSize: 16,
    fontWeight: 'bold',
    marginTop: '5px',
  },
  box: {
    display: 'inline-block',
  },
};

class UserContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      clicked: 0,
    };
    this.cardClicked = this.cardClicked.bind(this);
  }

  cardClicked(event) {
    this.setState({ clicked: event });
  }

  render() {
    const { classes } = this.props;
    const { clicked } = this.state;
    if (this.state.loading) return <CircularProgress />;
    if (clicked === 0)
      return (
        <div className={classes.headerContainer}>
          <Typography className={classes.title}>
            {language[this.props.selectedLanguage].projectSidebar.projectReports}
          </Typography>
          <div className={classes.line} />
          <div className={classes.grid}>
            <CardGroup className="m-5 d-block" style={{ textAlign: 'center' }}>
              <Card tag="1" onClick={() => this.cardClicked(1)} className={classes.box} style={{ width: '20rem', height: '15rem', padding: '15px', textAlign: 'center', cursor: 'pointer' }}>
                <Card.Img variant="top" src={Graph1} />
                <Card.Body>
                  <Card.Title style={styles.cardTitle}>{language[this.props.selectedLanguage].report.users}</Card.Title>
                  <Card.Text style={styles.text}>
                    {language[this.props.selectedLanguage].reportExplanations.users}
                  </Card.Text>
                </Card.Body>
              </Card>
              <Card tag="2" onClick={() => this.cardClicked(2)} className={classes.box} style={{ width: '20rem', height: '15rem', padding: '15px', textAlign: 'center', cursor: 'pointer' }}>
                <Card.Img variant="top" src={Graph2} />
                <Card.Body>
                  <Card.Title style={styles.cardTitle}>{language[this.props.selectedLanguage].report.hours}</Card.Title>
                  <Card.Text style={styles.text}>
                    {language[this.props.selectedLanguage].reportExplanations.hours}
                  </Card.Text>
                </Card.Body>
              </Card>
              <Card tag="3" onClick={() => this.cardClicked(3)} className={classes.box} style={{ width: '20rem', height: '15rem', padding: '15px', textAlign: 'center', cursor: 'pointer' }}>
                <Card.Img variant="top" src={Graph3} />
                <Card.Body>
                  <Card.Title style={styles.cardTitle}>{language[this.props.selectedLanguage].report.trazability}</Card.Title>
                  <Card.Text style={styles.text}>
                    {language[this.props.selectedLanguage].reportExplanations.trazability}
                  </Card.Text>
                </Card.Body>
              </Card>
              <Card tag="4" onClick={() => this.cardClicked(4)} className={classes.box} style={{ width: '20rem', height: '15rem', padding: '15px', textAlign: 'center', cursor: 'pointer' }}>
                <Card.Img variant="top" src={Graph4} />
                <Card.Body>
                  <Card.Title style={styles.cardTitle}>{language[this.props.selectedLanguage].report.timeline}</Card.Title>
                  <Card.Text style={styles.text}>
                    {language[this.props.selectedLanguage].reportExplanations.timeline}
                  </Card.Text>
                </Card.Body>
              </Card>
              <Card tag="5" onClick={() => this.cardClicked(5)} className={classes.box} style={{ width: '20rem', height: '15rem', padding: '15px', textAlign: 'center', cursor: 'pointer' }}>
                <Card.Img variant="top" src={Graph5} />
                <Card.Body>
                  <Card.Title style={styles.cardTitle}>{language[this.props.selectedLanguage].report.burnup}</Card.Title>
                  <Card.Text style={styles.text}>
                    {language[this.props.selectedLanguage].reportExplanations.burnup}
                  </Card.Text>
                </Card.Body>
              </Card>
              <Card tag="6" onClick={() => this.cardClicked(6)} className={classes.box} style={{ width: '20rem', height: '15rem', padding: '15px', textAlign: 'center', cursor: 'pointer' }}>
                <Card.Img variant="top" src={Graph6} />
                <Card.Body>
                  <Card.Title style={styles.cardTitle}>{language[this.props.selectedLanguage].report.burndown}</Card.Title>
                  <Card.Text style={styles.text}>
                    {language[this.props.selectedLanguage].reportExplanations.burndown}
                  </Card.Text>
                </Card.Body>
              </Card>
              <Card tag="7" onClick={() => this.cardClicked(7)} className={classes.box} style={{ width: '20rem', height: '15rem', padding: '15px', textAlign: 'center', cursor: 'pointer' }}>
                <Card.Img variant="top" src={Graph7} />
                <Card.Body>
                  <Card.Title style={styles.cardTitle}>{language[this.props.selectedLanguage].report.pullrequest}</Card.Title>
                  <Card.Text style={styles.text}>
                    {language[this.props.selectedLanguage].reportExplanations.pullrequest}
                  </Card.Text>
                </Card.Body>
              </Card>
              <Card tag="8" onClick={() => this.cardClicked(8)} className={classes.box} style={{ width: '20rem', height: '15rem', padding: '15px', textAlign: 'center', cursor: 'pointer' }}>
                <Card.Img variant="top" src={Graph8} />
                <Card.Body>
                  <Card.Title style={styles.cardTitle}>{language[this.props.selectedLanguage].report.lead}</Card.Title>
                  <Card.Text style={styles.text}>
                    {language[this.props.selectedLanguage].reportExplanations.lead}
                  </Card.Text>
                </Card.Body>
              </Card>
              <Card tag="9" onClick={() => this.cardClicked(9)} className={classes.box} style={{ width: '20rem', height: '15rem', padding: '15px', textAlign: 'center', cursor: 'pointer' }}>
                <Card.Img variant="top" src={Graph9} />
                <Card.Body>
                  <Card.Title style={styles.cardTitle}>{language[this.props.selectedLanguage].report.commits}</Card.Title>
                  <Card.Text style={styles.text}>
                    {language[this.props.selectedLanguage].reportExplanations.commits}
                  </Card.Text>
                </Card.Body>
              </Card>
              <Card tag="10" onClick={() => this.cardClicked(10)} className={classes.box} style={{ width: '20rem', height: '15rem', padding: '15px', textAlign: 'center', cursor: 'pointer' }}>
                <Card.Img variant="top" src={Graph10} />
                <Card.Body>
                  <Card.Title style={styles.cardTitle}>{language[this.props.selectedLanguage].report.averagetaskcompletiontime}</Card.Title>
                  <Card.Text style={styles.text}>
                    {language[this.props.selectedLanguage].reportExplanations.averagetaskcompletiontime}
                  </Card.Text>
                </Card.Body>
              </Card>
              <Card tag="11" onClick={() => this.cardClicked(11)} className={classes.box} style={{ width: '20rem', height: '15rem', padding: '15px', textAlign: 'center', cursor: 'pointer' }}>
                <Card.Img variant="top" src={Graph11} />
                <Card.Body>
                  <Card.Title style={styles.cardTitle}>{language[this.props.selectedLanguage].report.estimatedhoursvariation}</Card.Title>
                  <Card.Text style={styles.text}>
                    {language[this.props.selectedLanguage].reportExplanations.estimatedhoursvariation}
                  </Card.Text>
                </Card.Body>
              </Card>
              <Card tag="12" onClick={() => this.cardClicked(12)} className={classes.box} style={{ width: '20rem', height: '15rem', padding: '15px', textAlign: 'center', cursor: 'pointer' }}>
                <Card.Img variant="top" src={Graph12} />
                <Card.Body>
                  <Card.Title style={styles.cardTitle}>{language[this.props.selectedLanguage].report.taskprogress}</Card.Title>
                  <Card.Text style={styles.text}>
                    {language[this.props.selectedLanguage].reportExplanations.taskprogress}
                  </Card.Text>
                </Card.Body>
              </Card>
              <Card tag="13" onClick={() => this.cardClicked(13)} className={classes.box} style={{ width: '20rem', height: '15rem', padding: '15px', textAlign: 'center', cursor: 'pointer' }}>
                <Card.Img variant="top" src={Graph13} />
                <Card.Body>
                  <Card.Title style={styles.cardTitle}>{language[this.props.selectedLanguage].report.workload}</Card.Title>
                  <Card.Text style={styles.text}>
                    {language[this.props.selectedLanguage].reportExplanations.workload}
                  </Card.Text>
                </Card.Body>
              </Card>
            </CardGroup>
          </div>
        </div >
      );
    else if (clicked === 1) return (<User projectId={this.props.projectId} />)
    else if (clicked === 2) return (<Hours projectId={this.props.projectId} />)
    else if (clicked === 3) return (<Trazability projectId={this.props.projectId} />)
    else if (clicked === 4) return (<Timeline projectId={this.props.projectId} />)
    else if (clicked === 5) return (<BurnUp projectId={this.props.projectId} />)
    else if (clicked === 6) return (<BurnDown projectId={this.props.projectId} />)
    else if (clicked === 7) return (<PR projectId={this.props.projectId} />)
    else if (clicked === 8) return (<Lead projectId={this.props.projectId} />)
    else if (clicked === 9) return (<Github projectId={this.props.projectId} />)
    else if (clicked === 10) return (<AverageTaskCompletionTime projectId={this.props.projectId} />)
    else if (clicked === 11) return (<EstimatedHoursVariation projectId={this.props.projectId} />)
    else if (clicked === 12) return (<TaskProgress projectId={this.props.projectId} />)
    else if (clicked === 13) return (<Workload projectId={this.props.projectId} />)
  }
}














UserContainer.propTypes = {
  classes: PropTypes.object.isRequired,
  projectId: PropTypes.string.isRequired,
  fetchVizHoursPerUser: PropTypes.func.isRequired,
  notifyError: PropTypes.func.isRequired,
  members: PropTypes.array.isRequired,
  changeUsersHours: PropTypes.func.isRequired,
  endDate: PropTypes.any.isRequired,
  startDate: PropTypes.any.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    members: allMembers(ownProps, state),
    endDate: state.project.userhoursEndDate,
    startDate: state.project.userhoursStarDate,
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      fetchVizHoursPerUser,
      notifyError,
      changeUsersHours,
    },
    dispatch,
  );
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withStyles(styles)(UserContainer));
