import React, { Component } from 'react';
import PropTypes from 'prop-types';
import equal from 'fast-deep-equal';
import CircularProgress from '../../../lib/CircularProgress';

import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';
import HighchartsMore from 'highcharts/highcharts-more';

import language from '../../../../constants/language';
import { connect } from 'react-redux';
HighchartsMore(Highcharts);

class PercentageRange extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
    };
    this.setOptions.bind(this);
  }

  componentWillMount() {
    this.setState({ loading: false });

  }

  componentDidUpdate(prevProps) {
    if (!equal(this.props.data, prevProps.data)) {
      const chartOptions = this.setOptions();
      Highcharts.chart('chart', chartOptions);
    }
    if (!equal(this.props.selectedLanguage, prevProps.selectedLanguage)) {
      const chartOptions = this.setOptions();
      Highcharts.chart('chart', chartOptions);
    }
  }


  setOptions() {
    const {boardsNames, rangeHours, averageHours, value} = this.formatData();
    const { selectedLanguage } = this.props;
    return {
      chart: {
        type: 'columnrange',
        inverted: true
      },
      accessibility: {
        description: 'chart'
      },
      title: {
        text: language[selectedLanguage].percentageRangeChart.title,
      },
      subtitle: {
        text: `${language[selectedLanguage].percentageRangeChart.underestimation}   |   ${language[selectedLanguage].percentageRangeChart.overestimation} `,

      },
      xAxis: [{
        categories: boardsNames,
        },{ // mirror axis on right side
          opposite: true,
          reversed: false,
          categories: boardsNames,
          linkedTo: 0,
          labels: {
              step: 1
      }}],
      yAxis: {
        title: {
          text: language[selectedLanguage].percentageRangeChart.yAxis,
        },
        min: -value,
        max: value,
        marker: 0,
      },
      tooltip: {
        valueSuffix: '%'
      },

      plotOptions: {
        columnrange: {
          dataLabels: {
            enabled: true,
            format: '{y}%'
          }
        }
      },
      legend: {
        enabled: false
      },
      series:[{
        name: language[selectedLanguage].percentageRangeChart.percentage,
        colorByPoint: true,
        data: rangeHours
      },
      {
        type: 'spline',
        name: language[selectedLanguage].percentageRangeChart.average,
          data: averageHours,
          lineWidth: 0,
          marker: {
            radius: 8,
            fillColor: '#ea5941'
          },
          states: {
            hover: {
                lineWidthPlus: 0
                      }
                  }
        }
      ]

    }
  }
  formatData() {
    const  {data}  = this.props;
    const boards = data.boards;
    const minHours = {};
    const maxHours = {};
    const averageHours = [];
    const sumHours = {};
    const taskCount = {};
    const rangeHours = [];
    const boardsNames = [];
    const range_values = [];
    data.tasks.forEach((task) => {
      let dif = ((task.reported_hours - task.estimated_hours)/task.estimated_hours)*100;
      if (parseFloat(task.estimated_hours) === 0){
        dif = (0-task.reported_hours)*100;
      }
      dif = parseFloat(dif.toFixed(3));
      if(!taskCount[task.board_id]){
        taskCount[task.board_id] = 0;
        }
        taskCount[task.board_id] += 1;

      if(!minHours[task.board_id]){
        minHours[task.board_id] = dif;
        }
        if (dif < minHours[task.board_id]){
          minHours[task.board_id] = dif;
        }
      if(!maxHours[task.board_id]){
        maxHours[task.board_id] = dif;
        }
        if (dif > maxHours[task.board_id]){
          maxHours[task.board_id] = dif;
        }
      if(!sumHours[task.board_id]){
        sumHours[task.board_id] = 0;
        }
        sumHours[task.board_id] += dif;

    });
    const boards_ids = Array.from(new Set(Object.keys(sumHours)));
    
    boards_ids.forEach((board)=>{
      averageHours.push(parseFloat((sumHours[board] / taskCount[board]).toFixed(3)));
      rangeHours.push([minHours[board], maxHours[board]]);
      const board_name = boards
      .filter(b => b.id === board)
        .map(b => b.name)

      boardsNames.push(board_name);

    });
    range_values.push(0);
    range_values.push(0);
    rangeHours.forEach((range)=>{
      if (range[1] > range_values[1]){
        range_values[1]*= 0;
        range_values[1] += range[1];
      }
      if (range[0] < range_values[0]){
        range_values[0]*= 0;
        range_values[0]+=range[0];
      }
    });
    let value = range_values[1];
    if (range_values[0]*-1 > range_values[1]){
      value = range_values[0]*-1;
    }
    return {
      'boardsNames': boardsNames,
      'rangeHours': rangeHours,
      'averageHours': averageHours,
      'value': value,
    };
  }

  render() {
    if (this.state.loading) return <CircularProgress />;
    const chartOptions = this.setOptions(); 
    return (
      <div>
        <div id="chart">
          <HighchartsReact highcharts={Highcharts} options={chartOptions}  />
        </div>
      </div>
    );
  }
}

PercentageRange.propTypes = {
  data: PropTypes.object.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    selectedLanguage: state.user.language,
  };
};


export default connect(
  mapStateToProps,
)(PercentageRange);