import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { withStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

// import Calendar from './viz/Calendar';
import Main from './viz/Main';
import Hours from './viz/Hours';
import User from './viz/User';
import Trazability from './viz/Trazability';
import Timeline from './viz/Timeline';
import BurnUp from './viz/BurnUp';
import BurnDown from './viz/BurnDown';
import PR from './viz/PR';
import Lead from './viz/Lead';
import Github from './viz/Github';
import AverageTaskCompletionTime from './viz/AverageTaskCompletionTime';
import EstimatedHoursVariation from './viz/EstimatedHoursVariation';
import TaskProgress from './viz/TaskProgress';
import WorkLoad from './viz/WorkLoad';
import CircularProgress from '../lib/CircularProgress';

import { selectProject } from '../../actions/user';
import { fetchGetProjects } from '../../actions/api/projects';
import language from '../../constants/language';

const drawerWidth = 200;

const VerticalTabs = withStyles(theme => ({
  flexContainer: {
    flexDirection: 'column'
  },
  indicator: {
    display: 'none',
  }
}))(Tabs)

const styles = () => ({
  root: {
    backgroundColor: 'white',
    display: 'flex',
  },
  drawerPaper: {
    position: 'relative',
    backgroundColor: '#F5F5F5',
    height: '100vh',
    width: drawerWidth,
  },
  content: {
    flex: 1,
  }
});

class ReportsContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: 13,
      loading: true,
    };
    this.handleChange = this.handleChange.bind(this);
  }

  componentWillMount() {
    this.props.selectProject(this.props.projectId);
    Promise.all([
      this.props.fetchGetProjects(),
    ]).then((responses) => {
      if (responses[0].ok) {
        this.setState({ loading: false });
      }
    });
  }

  handleChange(event, value) {
    this.setState({ value });
  }

  render() {
    const { classes, selectedLanguage } = this.props;
    const { value } = this.state;
    const color = '#293D40'; // '#ea5941'
    const langDict = language[selectedLanguage].report;
    if (this.state.loading) {
      return <CircularProgress />;
    }
    return (
      <div className={classes.root}>
        <Drawer
          variant="permanent"
          classes={{
            paper: classes.drawerPaper,
          }}
          containerStyle={{ height: '100vh', marginTop: 56, 'overflow-x': 'scroll', width: drawerWidth }}
        >
          <VerticalTabs
            value={value}
            onChange={this.handleChange}
            indicatorColor={color}
            textColor="primary"
            orientation="vertical"
            variant="scrollable"
            scrollButtons="auto"
            TabIndicatorProps={{
              style: {
                backgroundColor: color,
              },
            }}
          >
            <Tab style={value === 0 ? { color } : null} label={langDict.users} />
            <Tab style={value === 1 ? { color } : null} label={langDict.hours} />
            <Tab style={value === 2 ? { color } : null} label={langDict.trazability} />
            <Tab style={value === 3 ? { color } : null} label={langDict.timeline} />
            <Tab style={value === 4 ? { color } : null} label={langDict.burnup} />
            <Tab style={value === 5 ? { color } : null} label={langDict.burndown} />
            <Tab style={value === 6 ? { color } : null} label={langDict.pullrequest} />
            <Tab style={value === 7 ? { color } : null} label={langDict.lead} />
            <Tab style={value === 8 ? { color } : null} label={langDict.commits} />
            <Tab style={value === 9 ? { color } : null} label={langDict.averagetaskcompletiontime} />
            <Tab style={value === 10 ? { color } : null} label={langDict.estimatedhoursvariation} />
            <Tab style={value === 11 ? { color } : null} label={langDict.taskprogress} />
            <Tab style={value === 12 ? { color } : null} label={langDict.workload} />
          </VerticalTabs>
        </Drawer>
        <main className={classes.content}>
          {value === 0 && <User projectId={this.props.projectId} />}
          {value === 1 && <Hours projectId={this.props.projectId} />}
          {value === 2 && <Trazability projectId={this.props.projectId} />}
          {value === 3 && <Timeline projectId={this.props.projectId} />}
          {value === 4 && <BurnUp projectId={this.props.projectId} />}
          {value === 5 && <BurnDown projectId={this.props.projectId} />}
          {value === 6 && <PR projectId={this.props.projectId} />}
          {value === 7 && <Lead projectId={this.props.projectId} />}
          {value === 8 && <Github projectId={this.props.projectId} />}
          {value === 9 && <AverageTaskCompletionTime projectId={this.props.projectId} />}
          {value === 10 && <EstimatedHoursVariation projectId={this.props.projectId} />}
          {value === 11 && <TaskProgress projectId={this.props.projectId} />}
          {value === 12 && <WorkLoad projectId={this.props.projectId} />}
          {value === 13 && <Main projectId={this.props.projectId} />}
        </main>
      </div>
    );
  }
}

ReportsContainer.propTypes = {
  classes: PropTypes.any.isRequired,
  projectId: PropTypes.string.isRequired,
  selectProject: PropTypes.func.isRequired,
  fetchGetProjects: PropTypes.func.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    selectProject,
    fetchGetProjects,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(ReportsContainer));
