import React from 'react';
import PropTypes from 'prop-types';
import { ResponsiveBar } from '@nivo/bar';

const LeadViz = ({ data }) => {

  return (
    <ResponsiveBar
      data={data}
      keys={['x']}
      indexBy='title'
      margin={{ top: 30, right: 30, bottom: 30, left: 400 }}
      layout="horizontal"
      axisBottom={null}
      xScale={{
        type: "time",
        format: "%Y-%m-%d"
      }}
      xFormat="time:%Y-%m-%d"

    />
  );
};

LeadViz.propTypes = {
  data: PropTypes.array.isRequired,
};

export default LeadViz;
