class UserContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      pr_lead: [],
      repos: [],
    };
    this.copyClipboard = this.copyClipboard.bind(this);
    this.changeOption = this.changeOption.bind(this);
  }

  componentWillMount() {
    this.props.changeTrazabilityHours({
      selectedRepo: this.props.selectedRepo,
      startDate: this.props.startDate,
      endDate: this.props.endDate,
      user: this.props.userId,
    });
    this.props.fetchGetLinkedRepos(this.props.projectId).then((response) => {
      if (response.ok) {
        this.setState({ repos: response.body, loading: false });
      }
    });
  }

  copyClipboard() {
    var dummy = document.createElement("input");
    document.body.appendChild(dummy);
    var href = `${window.location.origin}/project/${this.props.projectId}/report/lead`
    dummy.setAttribute('value', href);
    dummy.select();
    document.execCommand("copy");
    document.body.removeChild(dummy);
  }

  changeOption(e) {
    this.props.changeTrazabilityHours({
      selectedRepo: e.target.value,
      startDate: this.props.startDate,
      endDate: this.props.endDate,
      user: this.props.user,
    });
    this.getPRLeadTime()
  }

  getPRLeadTime() {
    const { repos } = this.state;
    const body = { 'repo_id': repos[0].id };
    const pr_lead = [];
    this.props.fetchGetPRLeadTime(this.props.projectId, body).then((response) => {
      console.log(response);
      // this.setState({ loading: false });
      if (response.ok) {
        response.body.lead_time.pr.forEach(element => {
          pr_lead.push({ 'x': element.createdAt, 'title': element.title });
          pr_lead.push({ 'x': element.closedAt, 'title': element.title });
        })
        this.setState({ loading: false, pr_lead });
      }
    })
  }

  render() {
    if (this.state.loading) return <CircularProgress />;
    const { classes } = this.props;
    const { pr_lead } = this.state;
    return (
      <div className={classes.container} >
        <div className={classes.headerContainer}>
          <Typography className={classes.title}>
            {language[this.props.selectedLanguage].report.lead}
          </Typography>
          <Button size="small" color="secondary" onClick={this.copyClipboard} className={classes.shareButton}>
            <Typography className={classes.shareText}>
              {language[this.props.selectedLanguage].share}
            </Typography>
          </Button>
          <div
            style={{ display: 'flex', alignSelf: 'center', marginLeft: '10px' }}
          >
            <Typography className={classes.text}>{language[this.props.selectedLanguage].report.repository}:</Typography>
            <div className={classes.selectText}>
              <Select
                value={this.props.selectedRepo}
                onChange={this.changeOption}
              >
                {this.state.repos.map((r) => {
                  return (
                    <MenuItem value={r.name} key={r.id}>
                      {r.name}
                    </MenuItem>
                  );
                })}
              </Select>
            </div>
          </div>
          <div className={classes.line} />
        </div>
        <div style={styles.graph} className={classes.wrapper}>
          {pr_lead.length > 0 && <LeadViz data={pr_lead} />}
        </div>
      </div >
    );
  }
}

UserContainer.propTypes = {
  classes: PropTypes.object.isRequired,
  projectId: PropTypes.string.isRequired,
  users: PropTypes.array.isRequired,
  userId: PropTypes.string.isRequired,
  fetchVizHoursPerTask: PropTypes.func.isRequired,
  fetchVizGithub: PropTypes.func.isRequired,
  fetchGetLinkedRepos: PropTypes.func.isRequired,
  fetchGetPR: PropTypes.func.isRequired,
  fetchGetCommitsPerPR: PropTypes.func.isRequired,
  fetchGetPRLeadTime: PropTypes.func.isRequired,
  changeTrazabilityHours: PropTypes.func.isRequired,
  notifyError: PropTypes.func.isRequired,
  selectedRepo: PropTypes.any.isRequired,
  user: PropTypes.any.isRequired,
  endDate: PropTypes.any.isRequired,
  startDate: PropTypes.any.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  return {
    userId: state.auth.id,
    selectedRepo: state.project.trazabilityhoursRepository,
    user: state.project.trazabilityhoursUser,
    endDate: state.project.trazabilityhoursEndDate,
    startDate: state.project.trazabilityhoursStarDate,
    users: allMembers(ownProps, state),
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      fetchVizHoursPerTask,
      fetchGetPR,
      fetchGetPRLeadTime,
      fetchGetCommitsPerPR,
      fetchVizGithub,
      fetchGetLinkedRepos,
      changeTrazabilityHours,
      notifyError,
    },
    dispatch,
  );
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withStyles(styles)(UserContainer));
