import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import moment from 'moment';
import 'moment/locale/es';

import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

import DayPickerInput from 'react-day-picker/DayPickerInput';
import 'react-day-picker/lib/style.css';

import CircularProgress from '../../../lib/CircularProgress';

import Timeline from './Timeline';

import { fetchUsersHoursTimeline } from '../../../../actions/api/viz';
import { changeTimelineHours } from '../../../../actions/project';
import { notifyError } from '../../../../actions/notification';
import language from '../../../../constants/language';

const styles = {
  wrapper: {
    height: '400px',
    width: 'auto',
  },
  modalContent: {
    width: '600px',
    height: '400px',
  },
  modalContainer: {
    // width: '800px',
  },
  title: {
    fontSize: '28px',
    fontFamiliy:
      '-apple-system, ".SFNSText-Regular", "San Francisco", BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", "Lucida Grande", Arial, sans-serif',
    color: 'rgb(68, 68, 68)',
  },
  headerContainer: {
    margin: '30px',
    width: 'auto',
  },
  text: {
    color: '#2a3d41',
    fontSize: 16,
    fontWeight: 'bold',
    marginTop: '10px',
    marginBottom: '10px',
  },
  selectText: {
    color: '#2a3d41',
    fontSize: 14,
    fontWeight: 'normal',
    marginTop: '10px',
    marginBottom: '10px',
    marginLeft: '10px',
  },
  grid: {
    display: 'flex',
    width: '100%',
    margin: '10px',
  },
  line: {
    width: 'auto',
    height: '1px',
    backgroundColor: '#CCCCCC',
  },
  info: {
    color: '#2a3d41',
    fontSize: 16,
    fontWeight: 'bold',
    marginTop: '5px',
  },
  box: {
    display: 'flex',
    marginRight: '10px',
    padding: '5px',
  },
  customIcon: {
    height: '24px',
    width: '24px',
    display: 'block',
    'border-radius': '50%',
    marginRight: '5px',
  },
  shareText: {
    fontSize: 12,
    color: 'white',
    margin: '0px 0px',
  },
  shareButton: {
    margin: 5,
    padding: '0px 8px',
    borderColor: 'white',
    border: '1px solid #fff',
    backgroundColor: '#ea5941',
    '&:hover': {
      backgroundColor: '#ca3921',
    },
    color: '#fff',
    borderRadius: 0,
  },
};

class UserContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      data: null,
    };
    this.changeFrom = this.changeFrom.bind(this);
    this.changeTo = this.changeTo.bind(this);
    this.changeData = this.changeData.bind(this);
    this.copyClipboard = this.copyClipboard.bind(this);
  }

  componentWillMount() {
    this.changeData(this.props.startDate, this.props.endDate);
  }

  copyClipboard() {
    var dummy = document.createElement("input");
    document.body.appendChild(dummy);
    var href = `${window.location.origin}/project/${this.props.projectId}/report/timeline/${this.props.startDate}/${this.props.endDate}`
    dummy.setAttribute('value', href);
    dummy.select();
    document.execCommand("copy");
    document.body.removeChild(dummy);
  }

  changeData(startDate, endDate) {
    this.props
      .fetchUsersHoursTimeline(this.props.projectId, {
        start_date: startDate,
        end_date: endDate,
      })
      .then((response) => {
        if (response.ok) {
          this.setState({
            loading: false,
            data: response.body.data,
          });
        }
      });
  }

  changeFrom(e) {
    const value = moment(e).format('YYYY-MM-DD');

    if (value && value <= this.props.endDate) {
      this.props.changeTimelineHours({
        startDate: value,
        endDate: this.props.endDate,
      });
      this.changeData(value, this.props.endDate);
    } else {
      this.props.notifyError('Invalid date');
    }
  }

  changeTo(e) {
    const value = moment(e).format('YYYY-MM-DD');

    if (value && value >= this.props.startDate) {
      this.props.changeTimelineHours({
        startDate: this.props.startDate,
        endDate: value,
      });
      this.changeData(this.props.startDate, value);
    } else {
      this.props.notifyError('Invalid date');
    }
  }

  render() {
    const { classes, selectedLanguage } = this.props;
    if (this.state.loading) return <CircularProgress />;
    const { data } = this.state;
    moment.locale(selectedLanguage);
    return (
      <div className={classes.headerContainer}>
        <Typography
          className={classes.title}
        >
          {language[selectedLanguage].report.timeline}
        </Typography>
        <Button size="small" color="secondary" onClick={this.copyClipboard} className={classes.shareButton}>
          <Typography className={classes.shareText}>
            {language[this.props.selectedLanguage].share}
          </Typography>
        </Button>
        <div className={classes.line} />
        <div className={classes.grid}>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
              marginLeft: '10px',
            }}
          >
            <Typography
              className={classes.text}
            >
              {language[selectedLanguage].report.from}:&nbsp;
            </Typography>
            <DayPickerInput
              value={this.props.startDate}
              onDayChange={this.changeFrom}
              dayPickerProps={{
                disabledDays: {
                  after: moment().toDate(),
                },
              }}
            />
          </div>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
              marginLeft: '10px',
            }}
          >
            <Typography
              className={classes.text}
            >
              {language[selectedLanguage].report.to}:&nbsp;
            </Typography>
            <DayPickerInput
              value={this.props.endDate}
              onDayChange={this.changeTo}
              dayPickerProps={{
                disabledDays: {
                  before: moment(this.props.startDate).toDate(),
                  after: moment().toDate(),
                },
              }}
            />
          </div>
        </div>
        <div className={classes.line} />
        <div className={classes.wrapper}>
          {data.length > 0 && <Timeline data={data} selectedLanguage={selectedLanguage}/>}
          {!data.length && (
            <Typography style={{ textAlign: 'center' }}>
              {' '}
              {language[selectedLanguage].report.noReportedHoursInPeriod}.{' '}
            </Typography>
          )}
        </div>
      </div>
    );
  }
}

UserContainer.propTypes = {
  classes: PropTypes.object.isRequired,
  projectId: PropTypes.string.isRequired,
  fetchUsersHoursTimeline: PropTypes.func.isRequired,
  notifyError: PropTypes.func.isRequired,
  changeTimelineHours: PropTypes.func.isRequired,
  endDate: PropTypes.any.isRequired,
  startDate: PropTypes.any.isRequired,
  selectedLanguage: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    endDate: state.project.timelinehoursEndDate,
    startDate: state.project.timelinehoursStarDate,
    selectedLanguage: state.user.language,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators(
    {
      fetchUsersHoursTimeline,
      changeTimelineHours,
      notifyError,
    },
    dispatch,
  );
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(withStyles(styles)(UserContainer));
